@-webkit-keyframes carding {
    0% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: translate3d(-100vw, 0vh, 0px); /* Safari */
        transform: translate3d(-100vw, 0vh, 0px);
    }
    100% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: translate3d(0px, 0px, 0px); /* Safari */
        transform: translate3d(0px, 0px, 0px);
    }
}

@keyframes carding {
    0% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: translate3d(-100vw, 0vh, 0px); /* Safari */
        transform: translate3d(-100vw, 0vh, 0px);
    }
    100% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: translate3d(0px, 0px, 0px); /* Safari */
        transform: translate3d(0px, 0px, 0px);
    }
}

.card {
    -webkit-perspective: 1000px;
            perspective: 1000px;
    position: static;
    width: 95px;
    height: 135px;
    -webkit-animation: carding 1s ease;
            animation: carding 1s ease;
}

@-webkit-keyframes backing {
    0% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(-180deg); /* Safari */
        transform: rotateY(-180deg);
    }
    100% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(0deg); /* Safari */
        transform: rotateY(0deg);
    }
}

@keyframes backing {
    0% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(-180deg); /* Safari */
        transform: rotateY(-180deg);
    }
    100% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(0deg); /* Safari */
        transform: rotateY(0deg);
    }
}

@-webkit-keyframes fronting {
    0% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(0deg); /* Safari */
        transform: rotateY(0deg);
    }
    100% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(180deg); /* Safari */
        transform: rotateY(180deg);
    }
}

@keyframes fronting {
    0% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(0deg); /* Safari */
        transform: rotateY(0deg);
    }
    100% { /* IE 9 */ /* Firefox */ /* Opera */
        -webkit-transform: rotateY(180deg); /* Safari */
        transform: rotateY(180deg);
    }
}

