.front {
    position: absolute;
    -webkit-transition: 1s;
    transition: 1s;
    background: -webkit-linear-gradient(bottom, #462d69, #533879);
    background: linear-gradient(to top, #462d69, #533879);
    background: -webkit-linear-gradient(45deg,rgba(0, 0, 0, 0) 48%,rgba(0, 0, 0, 0.2) 50%,rgba(0, 0, 0, 0) 52%),-webkit-linear-gradient(135deg,rgba(0, 0, 0, 0) 48%,rgba(0, 0, 0, 0.2) 50%,rgba(0, 0, 0, 0) 52%);
    background: linear-gradient(45deg,rgba(0, 0, 0, 0) 48%,rgba(0, 0, 0, 0.2) 50%,rgba(0, 0, 0, 0) 52%),linear-gradient(-45deg,rgba(0, 0, 0, 0) 48%,rgba(0, 0, 0, 0.2) 50%,rgba(0, 0, 0, 0) 52%);
    background-size: 1em 1em;
    background-color: #533879;
    width: 95px;
    height: 135px;
    border: 1px solid #2b1747;
    border-radius: 10px;
    box-shadow: 7px 7px 10px rgba(0,0,0,0.4);
}

.back {
    position: absolute;
    background: #eff3ef;
    background: -webkit-linear-gradient(bottom, #cecece, #eff3ef);
    background: linear-gradient(to top, #cecece, #eff3ef);
    width: 95px;
    height: 135px;
    border: 1px solid #8c8f8d;
    border-radius: 10px;
    box-shadow: 7px 7px 10px rgba(0,0,0,0.4);
}

.back,.front {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

